<td>wrapClassName</td>
<td></td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td></td>
<td>number</td>
<td>520</td>
</tr>
<tr>
<td>modalStyle</td>
<td></td>
<td>object</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="modal-events"><a class="header-anchor" href="#modal-events" aria-hidden="true"></a> Modal Events</h3>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>ok</td>
<td></td>
<td>-</td>
</tr>
<tr>
<td>cancel</td>
<td></td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-modal-xxx"><a class="header-anchor" href="#vue-modal-xxx" aria-hidden="true"></a> Vue.$modal.xxx()</h3>
<p></p>
<ul>
<li><code v-pre="">Modal.info</code></li>
<li><code v-pre="">Modal.success</code></li>
<li><code v-pre="">Modal.error</code></li>
<li><code v-pre="">Modal.warning</code></li>
<li><code v-pre="">Modal.confirm</code></li>
</ul>
<p> object</p>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>iconType</td>
<td> Icon </td>
<td>string</td>
<td>question-circle</td>
</tr>
<tr>
<td>title</td>
<td></td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td></td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td></td>
<td>number</td>
<td>416</td>
</tr>
