</undefined></template>
<style>
    .vertical-center-modal {
        display: flex;
        align-items: center;
        justify-content: center;
    }
</style>
<script>
    export default {
        data () {
            return {
                confirmLoading: false,
                visible: false,
                customFooterVisible: false,
                visible3: false,
                customTextVisible: false,
                customStyleVisible: false,
                customStyleVisible1: false,
                asyncModalVisible: false,
                customFooterLoading: false,
                asyncConfirmLoading: false,
                modalStyle: {
                    top: '20px',
                },
                wrapClassName: 'vertical-center-modal'
            }
        },    
        methods: {
            showModal () {
                this.visible = true;
            },

            handleOk () {
                this.confirmLoading = false;
                this.visible = false;
            },

            handleCancel () {
                this.visible = false;
            },

            showCustomFooter () {
                this.customFooterVisible = true;
            },

            customFooterOk () {
                /*  */
                this.customFooterLoading = true;
                setTimeout(() => {
                    this.customFooterVisible = false;
                    this.customFooterLoading = false;
                }, 2000);
            },

            customFooterCancel () {
                this.customFooterVisible = false;
            },

            showCustomTextModal () {
                this.customTextVisible = true;
            },

            customTextOk () {
                this.customTextVisible = false;
            },

            customTextCancel () {
                this.customTextVisible = false;
            },

            showCustomStyleModal () {
                this.customStyleVisible = true;
            },

            customStyleOK () {
                this.customStyleVisible = false;
            },

            customStyleCancel () {
                this.customStyleVisible = false;
            },

            showCustomStyleModal1 () {
                this.customStyleVisible1 = true;
            },

            customStyleOK1 () {
                this.customStyleVisible1 = false;
            },

            customStyleCancel1 () {
                this.customStyleVisible1 = false;
            },

            /* Async Modal */
            showAsyncModal () {
                this.asyncModalVisible = true;
            },

            handleAsyncOk () {
                /*  */
                this.asyncConfirmLoading = true;
                setTimeout(() => {
                    this.asyncModalVisible = false;
                    this.asyncConfirmLoading = false;
                }, 2000);
            },

            handleAsyncCancel () {
                this.asyncModalVisible = false;
            },

            showCustomTextConfirm() {
                this.$modal.confirm({
                    title: 'Confirm',
                    content: 'Bla bla ...',
                    okText: 'OK',
                    cancelText: 'Cancel'
                })
            },

            showConfirm () {
                this.$modal.confirm({
                    title: '',
                    content: '',
                    onOk: function () {
                        console.log('')
                    },
                    onCancel: function () {
                    }
                })
            },

            showAsyncConfirm () {
                this.$modal.confirm({
                    title: '',
                    content: ' 1 ',
                    onOk: async () => {
                        await new Promise(function (resolve) {
                            setTimeout(resolve, 1000);
                        })
                    },
                    onCancel: function () {
                        return new Promise(function (resolve) {
                            setTimeout(resolve, 1000);
                        })
                    }
                })
            },
             
            showCustomStyleConfirm () {
                this.$modal.confirm({
                    iconType: 'frown',
                    title: '',
                    content: '',
                    okText: '',
                    cancelText: '',
                    width: 300,
                })
            },

            info() {
                this.$modal.info({
                    maskClosable: true,
                    title: '',
                    content: ''
                })
            },

            success() {
                this.$modal.success({
                    maskClosable: true,
                    title: '',
                    content: ''
                })
            },

            error() {
                this.$modal.error({
                    title: '',
                    content: ''
                })
            },

             warning() {
                 this.$modal.warning({
                     title: '',
                     content: ''
                 })
             },

            _confirmDestroy() {
                const modal = this.$modal.success({
                    title: '',
                    content: ''
                });
                setTimeout(() => modal.destroy(), 1000);
            }
        }
    }
</script>


